
"use client";
import { useEffect, useState } from 'react';

export default function AccountsTable() {
  const [accounts, setAccounts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/accounts')
      .then(res => res.json())
      .then(data => {
        setAccounts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <div>Loading accounts...</div>;

  // Group accounts by type for Kanban columns
  const types = Array.from(new Set(accounts.map((a: any) => a.type)));
  const grouped = types.map(type => ({
    type,
    accounts: accounts.filter((a: any) => a.type === type)
  }));

  return (
    <div style={{ display: 'flex', gap: 24, alignItems: 'flex-start', marginTop: 24 }}>
      {grouped.map(col => (
        <div key={col.type} style={{ flex: 1, background: '#f4f5f7', borderRadius: 8, padding: 16, minWidth: 250 }}>
          <h3 style={{ textAlign: 'center', marginBottom: 16 }}>{col.type}</h3>
          {col.accounts.length === 0 && <div style={{ color: '#888', textAlign: 'center' }}>No accounts</div>}
          {col.accounts.map((acc: any) => (
            <div key={acc.id} style={{ background: '#fff', borderRadius: 6, boxShadow: '0 1px 4px rgba(0,0,0,0.07)', padding: 16, marginBottom: 16 }}>
              <div style={{ fontWeight: 600, fontSize: 16 }}>{acc.name}</div>
              <div style={{ fontSize: 13, color: '#555', margin: '4px 0 8px 0' }}>Owner: {acc.ownerUserId}</div>
              <div style={{ fontSize: 12, color: '#888' }}>Created: {new Date(acc.createdAt).toLocaleDateString()}</div>
            </div>
          ))}
        </div>
      ))}
    </div>
  );
}
